.AdminDashboardCardTitle{
    margin: 20px 20px 5px 20px;
    font-weight: 400;
    font-size: 18px;
    color: #a6a4a4;
}
.InvoiceList{
    width: 100%;
}
.InvoiceItem{
    margin: 5px;
    padding: 5px;
    border: 1px solid grey;
    display: flex;
    justify-content: space-between;
}
.InoviceItem-Car{
    display: flex;
    justify-content: flex-start;
}
.InvoiceTable{
    width: 100%;
    background-color: white;
    
}
.InvoiceTableImage{
    border-radius: 1%;
    margin: 5px;
    padding: 5px;
    width: 120px;
    height: 100px;
    border: 1px solid #cbcbcb;
}
.Inquiry-Table-Title-Item{
    margin: 5px;
}
.Inquiry-Table-Body-Item{
    text-align: center;
    text-align: -webkit-center;
}
.InvoiceSpeech{
     border: 1px solid #d9d9d9; 
     margin: 20px; 
     box-shadow: -1px 0px 2px 3px #ececec; 
     background-color: white; 
     border-radius: 10px;
     height: 70vh;
     overflow: auto;
}
.OrderResult{
    margin-top: 5px;
    padding: 5px 15px 5px 15px;
    border-radius: 3px;
    border: 1px solid grey;
}
/**reactTable**/
.rt-thead{
    background-color: #e8ecf3!important;
    box-shadow: 0 2px 15px 0 rgba(0,0,0,0)!important;
}
.rt-td{
    align-self: center;
}
/*how to*/
.Howto-ToolArea{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.HowTo-Item{
    border: 1px solid #606770;
    width: 100%;
    padding: 10px;
    margin: 10px;
    border-radius: 2px;
}
.HowTo-Item-Title{
    cursor: pointer;
    width: 100%;
    /* font-size: 19px; */
    text-align: center;
    display: flex;
}
.HowTo-smallItem{
    margin-top: 10px;
    width: 100%;
    padding: 10px;
    border: 1px solid #cfc1c1;
    border-radius: 3px;
}
.HowTo-smallItem-Title{
    text-align: left;
    width: 100%;
    font-size: 25px;
    margin-bottom: 20px;
    position: relative;
}
.Howto-smallItem-Textarea{
    /* padding: 10px; */
    /* margin:10px */
    margin-top: 10px;
    margin-bottom: 10px;

}
.Howto-smallItem-TextItem{
    /* padding: 10px; */
    margin:10px;

    overflow-wrap: anywhere;
}
.Howto-smallItem-Imagearea{
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
}
.Howto-smallItem-ImageItem{
    width: 100%;
}
.Howto-smallItem-ImageItem >img{
    width: 100%;
}

