/************/
.dropbtn {
    width: 100%;
    font-size: 20px;
    font-weight: 400;
    position: relative;
  }
.dropbtn-img{
    position: absolute;
    left: 35px;
    width: 20px;
}
.dropbtn-img img{
    width: 100%;
}
.dropbtn-text{
    margin-left: 30px;
}

  /* The container <div> - needed to position the dropdown content */
  .dropdown {
    position: relative;
    display: inline-block;
    width: 100%;
  }
  .MyDropDown{
    position: relative;
    cursor: pointer;
    width: 100%;
    margin-right: 30px;
    /* min-width: 250px; */
  }
  .MyDropDown-Header-false{
    /* display: flex;
    justify-content: space-between; */
    /* margin: 5px; */
    /* border-top-left-radius: 10px; */
    border: 1px solid;
    /* border-top-right-radius: 10px; */
    /* padding: 5px 10px 5px 10px; */
    padding: 7px 15px 7px 15px;

    border-radius: 7px;
    min-height: 50px;
    position: relative;
    white-space: nowrap;
    display: flex;

  }
  .MyDropDown-Header-true{
    /* display: flex;
    justify-content: space-between; */
    /* margin: 5px; */
    border-top-left-radius: 7px;
    border: 1px solid;
    border-top-right-radius: 7px;
    /* padding: 5px 10px 5px 10px; */
    padding: 7px 15px 7px 15px;
    min-height: 50px;
    position: relative;
    white-space: nowrap;
    display: flex;
    
  }
  .Header-Img{
    position: absolute;
    top: 5px;
    left: 5px;
  }
  .Header-Color-secondary{
    background-color: rgba(73, 154, 236, 1);
  }
  .Header-Color-primary{
    background-color:white;
  }
  .Header-Outline-true{
    border: 1px solid #cdcdcd;
  }
  .Header-Outline-false{
    border: none;
  }
  .MyDropDown-Header-Drop{

    position: absolute;
    right: 15px;
    top: 10px;
    width: 15px;
  }
  .Title-Color-primary{
    margin: 0px;
  }
  .Title-Color-secondary{
    color: white;
    margin: 0px;
  }
  .MyDropDown-Img{
    border-radius: 50%;
    width: 25px;
    height: 25px;
    margin: 5px ;
    border: 1px solid black;
  }
  /* Dropdown Content (Hidden by Default) */
  .MyDropDown-Content {
    position: absolute; 
    min-width: 160px;
    /* box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2); */
    z-index: 1;
    width: 100%;
    /* border: 1px solid grey;  */
    margin-top: 0px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: white;
    margin-top: 5px;
    border: 1px solid #cbcbcb;
    /* padding: 5px; */
  }
  .MyDropDown-Content-Item{
    padding: 5px 15px 5px 15px;
    min-height: 50px;
    overflow-wrap: anywhere;

  }
  .MyDropDown-Content-Item:hover{
    background-color: rgb(245, 245, 245);
  }
  .MyDropDown-Content-Item > .Checkbox{
    float: right;
    margin-right: 5px;
    margin-top: 10px;
  }
  /* Links inside the dropdown */


  /* Change color of dropdown links on hover */
  .dropdown-content a:hover {background-color: #ddd}

  /* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
  .Show {display:block;}