.Carbody{
    padding: 5px;
    margin: 5px;

    width: 50%;
    position: relative;
    background-color: #e7e7e7;
    border-radius: 10px;
    cursor: pointer;
}
.CarbodySelected{
    
    box-shadow: 0px 0px 2px 2px rgba(73, 154, 236, 1);
    border-radius: 10px;
}
.Advanced-Accordian{
    border-bottom: 1px solid rgba(0,0,0,0.2);
    /* margin-left: 10px;
    margin-right: 10px; */
}
.SelectCarBodyBadge{
    background-color: rgba(73, 154, 236, 1);
    border-radius: 50%;
    top: -10px;
    right:-10px;
    /* transform: translateX(-50%); */
    position: absolute;
    width: 20px;
    height: 20px;
    z-index: 1;
}
.CarBodyTypeName-true{
    text-align: center;
    color: rgba(73, 154, 236, 1);
}
.CarBodyTypeName-false{
    text-align: center;
    color: grey;
}
.FilterSection{
    border-bottom: 1px solid rgb(207, 207, 207);
    padding-bottom: 10px;
    padding-top: 10px;
}
.FilterSectionHeader{
    padding-bottom: 5px;

}