.element{
    /* height: 500px; */
}
.HowToBuy-Content{
    font-family: Roboto, Helvetica, Arial, sans-serif;

}
.HowtoBuyStepbar{
    display: flex;
    /* height: 50px; */
    align-items: center;
    height: 90px;
}
.HowtoBuyStepbar_Item{
    font-weight: 500;
}
.HowtoBuyItem{
    background-color: white;
    padding: 20px 40px 20px 40px;
    border-radius: 3px;

    /* margin-top: 10px;*/
    margin-bottom: 10px; 
    
}
.HowtoBuy-Menu{
    position: sticky;
    top: 0px;
}
.HowToBuy-Title{
    height: 50px;
    font-size: 30px;
    margin-top: 20px;
    font-weight: 500;
    margin-bottom: 20px;
}
.HowtoBuy-Part{
    padding-left: 10px;
    padding-right: 10px;
    height: 100%;
}
.HowToBuy-List{
    border-radius: 3px;
   
    margin-right: 10px;
    background-color: white;
    padding: 10px 0px 10px 0px;
}
.HowtoBuyItemTitle{
    text-align: center;
    font-size: 25px;
    font-weight: 500;
}

.HowToBuy_LinkItem{
    color: black;
}
.HowToBuy_LinkItem .current{
    color:blue;
}
.HowtoBuySmallItemImagePart{
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
}
.HowtoBuyItemSmallTitle{
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 500;
}
.HowtoBuySmallItemTextItem{
    margin-bottom: 20px;
    opacity: .7;
}
.HowtoBuySmallItemImgItem{
    width: 100%;
    padding: 5px;
}
.HowtoBuySmallItemImgItem >img{
    width: 100%;
}

.HowToBuy-List-Item{
    padding: 10px 20px 10px 20px;
}
.active > .HowToBuy-List-Item{
    box-shadow: -3px 0px 0px 0px #499aec;
    background-color: #e6f2ff;
}
.HowToBuy-List-Item.current{
    /* box-shadow: -3px 0px 0px 0px #499aec;
    background-color: #e6f2ff; */
}