.Admin{
    /* display: flex; */
    display: flex;
  
}
.AdminSideBar{
    width: 270px;
}
.AdminSideBar>p{
    margin-left: 20px;
    margin-top: 20px;
    font-size: 30px;
    font-weight: 700;
}
.AdminContent{
    width: 100%;
    padding:15px;
    background-color: #f5f6f8;
}
.AdminContentMain{
    margin-top: 20px;
}
.AdminContentTop{
    height: 50px;
    /* text-align: -webkit-center; */
    padding: 10px;
}
.toggleMenu{
    display: none;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    text-align: center;
    align-items: center;
    color: white;
    font-size: 15px;
    background-color: #353535;
    text-align: -webkit-right;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
}
@media only screen and (max-width: 768px)
{
    .toggleMenu{
        display: flex;
        justify-content: center;
    }
    .toggleMenu.true{
        display: none;
    }
    .toggleMenu.false{
        display: flex;
    }
}