.TabHeader{
    padding: 0px;
    /* align-items: center; */
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #dbdbdb;
}
.TabContent{
    /* margin-top: 30px; */
    padding: 0px;
 
}
.TableContent-Row-Header{
    width: 100%;
    display: flex;
    /* justify-content: center; */
}

.Opacity70{
    opacity: 70%;
}
.NoPaddingMargin{
    padding: 0px!important;
    margin: 0px!important;
}
/*-----MyInquiry AND ORDER------*/
.TablePart{
    width: 100%;
    margin: 30px;


    border: 1px solid #dbdbdb;
    overflow-x: auto; 
}
.Table-Item {
    /* border: 0.5px solid #dbdbdb; */
    text-align: -webkit-center;
    
    vertical-align: auto;
}
.TableHeader{
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.TableHeader-Item{
    padding: 10px;
}
.THI-Index{
    
    width: 50px!important;

    min-width: 50px;
    /* align-self: center; */
}
.THI-Normal{
    
    min-width: 150px;
    
    width: calc((100%-50px)/5);
    border-left: 1px solid #dbdbdb;
    display: inline-block;
}
/*table content*/
.TableContent{
    background-color: white;
    width: 100%;
    min-height: 500px;
    background-color: white;
    border-top: 1px solid #dbdbdb;
}
.TableContent-Row{
    width: 100%;
    display: flex;
    /* justify-content: center; */
    border-bottom: 1px solid #dbdbdb;
}
.TableContent-Item{
    padding: 1%;
    border-top: 1px solid #dbdbdb;
    background-color: white;

}
.Table-Item-P{
    margin: 5px;
    width: 100%;
}
.Table-Item-P > a > img {
    width: 80%!important;
    box-shadow: 0px 0px 1px 1px #c7c7c7;
    padding: 3%;
}
.Empty-Content{

}
.DetailBack{
    padding: 15px 0px 20px 0px;
    width: 100%;
    border-bottom: 1px solid #dbdbdb;
}
.DetailInfoPanel{
    border-right: 1px solid #dbdbdb;
    height: 100%;
}
.InquiryContent{
    background-color: white;
    width: 100%;

}
.InquiryContent-Header{
    border-bottom: 1px solid #d1d1d1;
    margin:0px!important;
}
.InquiryContent-Content{
    margin:0px;
    padding: 0px;
}
.InquiryContent-Content-First{
    /* margin:5px; */
    padding: 5px;
    display: flex;
    /* justify-content: center; */
    border-right: 1px solid #d1d1d1;
}
.InquiryContent-Content-Second{
    /* margin:5px; */
    padding: 25px;
}
.InquiryContent-Content-One{
    padding: 5px;
    text-align: center;
    /* width: 50%; */
}
.InquiryContent-Content-One > img{
    width: 100%;
}
.Speech-Item{
    display: flex;
    padding: 10px;
}
.Speech-Left > .Speech-Content > .Speech-Detail{
    border-radius: 0px 10px 10px 10px;
    background-color: #F0F3F5;
}

.Speech-Right > .Speech-Content > .Speech-Time{
    text-align: right;
    
}
.Speech-Right > .Speech-Content > .Speech-Detail{
    border-radius: 15px 0px 15px 15px;
    background-color: #DEEEFF;
}
.Speech-Detail-Title{
    font-weight: bold;
    
}
.Speech-Detail-Content{
    opacity: .8;
    margin-top: 10px;
}
.Speech-Time{
    opacity: .5;
}
.Speech-Detail{
    padding: 20px;
}
.InquiryContent-Content-Second-Speech{
    height: 60vh;
    overflow: auto;
    
    /* border: 1px solid #d9d9d9; */
    margin: 10px;
    /* box-shadow: -1px 0px 2px 2px #ececec; */
    /* background-color: white; */
    border-radius: 3px;
}
.InquiryContent-Content-Second-Input{
    /* padding: 25px; */
    margin: 0px 10px 0px 10px;
    display: flex;
}
.InquiryContent-Content-Second-Input-Button{
    width: 20%;
}
.InquiryContent-Content-Second-Input-Field{
    width: 80%;
}
.Avatar{
    border-radius: 50%;
    margin: 15px;
    width: 100px;
    height: 100px;
    border:1px solid #8c8c8c;
    padding: 5px;
}
.Speech-Content{
    width: 100%;
    margin:15px;
}
.hasNewMsg{
    color: red;
}
/*-----Settings------*/
.SettingPart{
    margin: 30px;
    padding:30px;
    background-color: white;
    border-radius: 5px;
    width: 100%;
}

.Setting-Col-Part{
    /* margin-left: 20px; */
}
.Setting-Photo{
    /* margin:50px; */
    border-radius: 5px;
    border: 2px dashed rgba(73, 154, 236, 1);
    text-align: -webkit-center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
.Setting-Photo-Item{
    
    padding-top: 10%;
    padding-bottom: 10%;
}
.Setting-Photo-Item-Detail{
    padding: 2%;
}
.Setting-Photo-Item > img{
    width: 70%;
    min-width: 100px;
    border-radius: 50%;
    padding: 5px;
    border: 1px solid #c3d6ea;
}
.Setting-Content-Item{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 30px;
    padding-top: 20px;

}
.Item-Title{
    opacity: .7;
}
.SaveBtn{
    height: 45px;
    width: 10%;
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: red;
    opacity: 1; /* Firefox */
  }
@media only screen and (max-width: 768px)
{
    .SettingPart{
        margin: 5px;
        padding: 5px;
    }
    .TablePart{
        margin: 5px;
    }
    .Setting-Col-Part{
        margin-left: 0px;
    }
    .Setting-Content-Item{
      
        padding-left: 0px;
        padding-top: 20px;
    }
}