.MyNavBar{
    background-color: white!important;
}
.navbar{
    padding: 30px 40px 30px 40px!important;
    background-color: white;
}
.nav-item{
    margin-left: 10px;
    /* margin: 5px; */
}
.nav-link{
    padding: 0px!important;
}
.shadow {
    box-shadow: 0 0.01rem 0.01rem 0 rgba(155, 194, 184, 0) !important;
}


.no-gutters > .col,
.no-gutters > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
}

.align-items-center {
    align-items: center !important;
}

.navbar-light .navbar-brand{
    color: #00158f !important;
    font-size: 30px !important;
    font-family: 'Space Mono',monospace;
}

.navbar-light .navbar-toggler{
    border: none !important;
    color: rgba(73, 154, 236, 1) !important;
}

.navbar-light .navbar-toggler-icon{

  
}

.navbar-light .navbar-toggler-icon:hover{
    transform: scale(1.05) !important;
}

.navbar-light .navbar-toggler-icon:focus{
    outline: none !important;
}

.navbar-light .navbar-nav .nav-link{
    position: relative !important;
    font-size: 18px !important;
    padding: 5px 0px!important;
}
.nav-link{
    font-family: 'Lato';
    font-weight: bold;
    font-size: 12px;
    color: #363940;
}
.nav-link:hover{
    color: #6171ff4f;
    /* background: rgba(0, 17, 255, 0.1); */
}
.signlink
{
    font-family: 'Lato';
    font-weight: bold;
    font-size: 12px;
    color: #499AEC!important;
}
.signbtn{
    background-color: #499AEC!important;
    width: 150px!important;
}
.nav-link.login-register{
    background: #50c878;
    padding: 15px !important;
    margin: auto 10px;
    vertical-align: middle;
    border-radius: 20px;
}

.navbar-light .navbar-text i{
    color: #50c878;
    font-size: 30px;
}

@media (min-width: 992px) {
.navbar-expand-lg .navbar-collapse {
    display: flex!important;
    flex-basis: auto;
    /* padding: 0px 50px !important; */
    flex-grow: 2 !important;
}

.navbar-expand-lg .navbar-nav .nav-link {
    /* padding-right: 1rem !important;
    padding-left: 1rem !important; */
}
.dropdown{
    background-color: white;
}

}
@media (max-width : 768px){
    .navbar-light .navbar-brand{
        font-size : 20px !important;
    }

    .nav-link.login-register{
        margin: 0px;
    }
}